import React, { Component } from 'react';
import { Card, Row, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common';
import { getStudentTitles, deleteStudentTitles } from 'services/studentTitle';

class StudentTitles extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteStudentTitles(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Mã chức danh',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên chức danh',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.QUAN_LY_LOP__CHUC_DANH_SINH_VIEN__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh sách chức danh">
        <CartHeadAction>
          <Link to={routeConstant.QUAN_LY_LOP__CHUC_DANH_SINH_VIEN__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getStudentTitles}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StudentTitles />
  </Page>
);



